/*@use postcss-cssnext;*/

html {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    
}

*, *:before, *:after {
-webkit-box-sizing: inherit;
        box-sizing: inherit;
}

main {
    display: block;
}

ul, 
li, 
dl, 
dd,
p,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
}

:link:not(:hover) {
    text-decoration: none;
}

@-webkit-keyframes flip-out {
    0% {
      -webkit-transform: rotateX(0deg);
              transform: rotateX(0deg);
    }
    100% {
      -webkit-transform: rotateX(90deg);
              transform: rotateX(90deg);
      opacity: 0.4;
    }
  }

@keyframes flip-out {
    0% {
      -webkit-transform: rotateX(0deg);
              transform: rotateX(0deg);
    }
    100% {
      -webkit-transform: rotateX(90deg);
              transform: rotateX(90deg);
      opacity: 0.4;
    }
  }

@-webkit-keyframes flip-in {
    0% {
      -webkit-transform: rotateX(-90deg);
              transform: rotateX(-90deg);
      opacity: 0.4;
    }
    100% {
      -webkit-transform: rotateX(0deg);
              transform: rotateX(0deg);
    }
}

@keyframes flip-in {
    0% {
      -webkit-transform: rotateX(-90deg);
              transform: rotateX(-90deg);
      opacity: 0.4;
    }
    100% {
      -webkit-transform: rotateX(0deg);
              transform: rotateX(0deg);
    }
}

@-webkit-keyframes spin-out {
    0% {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
    }
    100% {
      -webkit-transform: rotateY(90deg);
              transform: rotateY(90deg);
      opacity: 0.4;
    }
  }

@keyframes spin-out {
    0% {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
    }
    100% {
      -webkit-transform: rotateY(90deg);
              transform: rotateY(90deg);
      opacity: 0.4;
    }
  }

@-webkit-keyframes spin-in {
    0% {
      -webkit-transform: 
        rotateY(90deg) 
        translateX(50%) 
        rotateY(-90deg)
        translateX(50%)
        rotateY(90deg);
              transform: 
        rotateY(90deg) 
        translateX(50%) 
        rotateY(-90deg)
        translateX(50%)
        rotateY(90deg)
    }
    100% {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
      -webkit-transform-origin: 0 25%;
              transform-origin: 0 25%;
    }
}

@keyframes spin-in {
    0% {
      -webkit-transform: 
        rotateY(90deg) 
        translateX(50%) 
        rotateY(-90deg)
        translateX(50%)
        rotateY(90deg);
              transform: 
        rotateY(90deg) 
        translateX(50%) 
        rotateY(-90deg)
        translateX(50%)
        rotateY(90deg)
    }
    100% {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
      -webkit-transform-origin: 0 25%;
              transform-origin: 0 25%;
    }
}

@-webkit-keyframes spinner {
    to {
        -webkit-transform: rotate(-1turn);
                transform: rotate(-1turn);
    }
}

@keyframes spinner {
    to {
        -webkit-transform: rotate(-1turn);
                transform: rotate(-1turn);
    }
}

