.cube__perspective {
    -webkit-perspective: 200vw;
            perspective: 200vw;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform-origin: top;
            transform-origin: top;
  }

@media (min-width: 1000px) {

    .cube {
        -webkit-transform-style: preserve-3d;
                transform-style: preserve-3d;
        -webkit-animation: spin-in 1s backwards 1 1s;
                animation: spin-in 1s backwards 1 1s;
        will-change: transform;
    
    }
    
}

.cube {
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    
}

.cube__face-1 {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background: #c47333;
    -webkit-transform:  rotateY(-90deg) translateX(-100%);
            transform:  rotateY(-90deg) translateX(-100%);
    -webkit-transform-origin: left;
            transform-origin: left;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    
}

.cube__face-1::before {
    content: "";
    height: 15vw;
    width: 15vw;
    margin-top: 33vh;
    border-radius: 50%;
    border: 1vw dotted white;
    border-bottom: 0 none;
        -webkit-animation: spinner 3s backwards infinite linear;
                animation: spinner 3s backwards infinite linear;
    
}

.cube__face-2 {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    
}

.cv {
  max-width: 1200px;
  margin: auto;
}

.cv__grid {
    display: grid;
    margin: 0;
    background: #fafcfa;
    padding: 4vw;
  }

@media (min-width: 1000px) {

    .cv__grid {
        padding: 4em;
    
    }
    
}

@media print {

    .cv__grid {
        width: 21cm;
        padding: 0.5cm 2cm;
        max-width: none;
    
    }
    
}

.grid {
  margin-bottom: 1em;
  /* background-position-y: -6px;
  background-image: linear-gradient(#ddd 0, #ddd 1px, transparent 1px);
  background-size: 21.55px 21.55px; */
}

@media (min-width: 1000px),
  print {

    .grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 3em;
        grid-template-areas: "one two-three two-three two-three";
    
    }
    
}

.grid__header {
    grid-area: one;
    
}

.grid__main {
    grid-area: two-three;
    
}

.grid__sub {
    margin-bottom: 21.28px;
    margin-bottom: 1.33rem;
    position: relative;
    
}

.grid__sub::before {
    content: "";
    position: absolute;
    left: 0;
    top: -7.2px;
    top: -0.45rem;
    width: 100%;
    border-top: 1.2px solid #4db0c4;
    border-top: 0.075rem solid #4db0c4;
    
}

@media (min-width: 600px),
    print  {

    .grid__sub {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 3em;
        grid-template-areas:
        "two three three";
    
    }
    
}

.grid__sub-info {
    grid-area: two;
    
}

.grid__sub-description {
    grid-area: three;
    
}

